import React from "react";
import {Container, Row, Col} from "react-bootstrap";
function Main2() {
    return (
        <Container>
            <Row
                style={{
                    justifyContent: "center",
                    paddingTop: "100px",
                    paddingBottom: "100px",
                }}>
                <Col md={12}>
                    <div className="article-title-box">
                        <p className="Blue-Title-Article">   .</p>
                        <p className="Blue-Title-Article">      .</p>
                    </div>
                </Col>
                <Col md={3}>
                    <div className="article-content-box">
                        <p className="Blue-Article">  VOD  <br/> </p>
                    </div>
                </Col>
                <Col md={3}>
                    <div className="article-content-box">
                        <p className="Blue-Article">   <br/> /   </p>
                    </div>
                </Col>
            </Row>
        </Container>
    );
}

export default Main2;