import React from "react";
import {Container, Row, Col} from "react-bootstrap";
function Main1() {
    return (
        <Container>
            <Row
                style={{
                    justifyContent: "center",
                    paddingTop: "100px",
                    paddingBottom: "100px"
                }}>
                <Col md={5}>

                    <div className="logo-box">
                        <img className="logo-square" alt="logo-sqaure" src="img/logo.png"></img>
                    </div>
                </Col>
                <Col md={5}>
                    <div className="logo-title-box">
                        <img className="logo-title" alt="logo-title" src="img/logo-title.png"></img>
                    </div>
                    <p className="Article">   <br/>      <br/>   IT  .</p>
                    <p className="Article" style={{ fontSize:"1em" }}>(2023   61   .)</p>
                </Col>
            </Row>
        </Container>
    );
}

export default Main1;